import { Component } from '@angular/core';
import { NavController,AlertController } from 'ionic-angular';
import { CountryProvider } from '../../providers/country/country';
import { ChannelProvider } from '../../providers/channel/channel';

@Component({
  selector: 'page-prayer',
  templateUrl: 'prayer.html'
})
export class PrayerPage {
    
    
    public subscription:any;
    public cities:Array<any>;
    public channels:Array<any>;
    
    constructor(public navCtrl: NavController, public countryProvider: CountryProvider, public channelProvider: ChannelProvider, private alertCtrl: AlertController) {
        
        this.subscription = {cityId:"", hubId:""};
        this.cities = [];
        this.channels = [];
        this.getCountries();
    }
    
    public getCountries(){
        this.countryProvider.getCountries().then((data: Array<any>) => {
            console.log(data[0].regionsList);
            this.cities = data[0].regionsList;
        })
    }
    
    public getChannels(){
        
        this.channels = [
                {
                  "channelName": "Channel 1",
                  "hubEventTopic": "Event Topic",
                  "hubId": "1",
                  "hubSalatTopic": "Salat Topic",
                  "streamingMount": "1",
                  "suburbsId": 0
                }
              ]
        
        /*
        this.channelProvider.getChannels(this.subscription.cityId).then((data:Array<any>) => {
            console.log(data);
            this.channels = data;
        })
        */
    }
    
    public selectChannel(id){
        this.subscription.hubId = id;
        let alert = this.alertCtrl.create({
          title: 'Subscribe',
          message: 'Are you sure you want to subscribe to this channel?',
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel'
            },
            {
              text: 'Yes',
              handler: () => {
                    
              }
            }
          ]
        });
        alert.present();              
    }

}



