import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {AppSettings} from '../../app/app.settings';
/*
  Generated class for the ChannelProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ChannelProvider {

    constructor(public http: HttpClient) {
      console.log('Hello ChannelProvider Provider');
    }
  
    public getChannels(cityId){
        return new Promise((resolve, reject) => {
            
            const headers = new HttpHeaders().set('authorization', 'test@test.com');         
            
            this.http.get(AppSettings.apiUrl + "/secured/channel/listbycity/" + cityId, { headers:headers }).subscribe((res) => {        
                    resolve(res);             
              },(e) => {
                  reject(e);
              });          
        })
    }    
  
  
    public subscribe(userEmail, hubId){
        return new Promise((resolve, reject) => {
            
         
            this.http.post(AppSettings.apiUrl + "/secured/subscribers/subscribe", {userEmail:userEmail, hubId:hubId}).subscribe((res) => {        
                    resolve(res);             
              },(e) => {
                  reject(e);
              });          
        })
    }  
    
    public unsubscribe(subscriberId){
        return new Promise((resolve, reject) => {
            this.http.delete(AppSettings.apiUrl + "/secured/subscribers/unsubscribe/" + subscriberId).subscribe((res) => {        
                    resolve(res);             
              },(e) => {
                  reject(e);
              });          
        })
    } 
    
    
    public getChannel(subscriberId){
        return new Promise((resolve, reject) => {
            this.http.get(AppSettings.apiUrl + "/secured/subscribers/channelview/" + subscriberId).subscribe((res) => {        
                    resolve(res);             
              },(e) => {
                  reject(e);
              });          
        })
    }         
    
    
}



