import { Component } from '@angular/core';
import { Platform, App } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { TabsPage } from '../pages/tabs/tabs';
import { LoginPage } from '../pages/login/login';
import { SignUpPage } from '../pages/sign-up/sign-up';

import { Storage } from '@ionic/storage';

import { AuthenticationProvider } from '../providers/authentication/authentication';

import {AppSettings} from './app.settings';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = LoginPage;

  constructor(public app: App, platform: Platform, statusBar: StatusBar, public splashScreen: SplashScreen, public authProvider: AuthenticationProvider, public storage: Storage) {
    platform.ready().then(() => {
        // Okay, so the platform is ready and our plugins are available.
        // Here you can do any higher level native things you might need.
        statusBar.styleDefault();

        this.storage.get("user").then((token) => {
            if (token){
                this.app.getRootNav().setRoot(TabsPage);
                this.app.getRootNav().popToRoot();                       

            }
            setTimeout(() => {this.splashScreen.hide()},500);
        }).catch(() => {
            this.splashScreen.hide();
        });      
      
      
    });
  }
}



